/**
 * Converts a TN of any format into a simple 10-digit string.
 *
 * @module
 *
 * @param {string} tn TN
 * @returns {string} simple 10-digit TN
 *
 * @example
 * import { stripTn } from 'js-util-lib';
 *
 * stripTn('+1(303)213-1000');
 * // '3032131000'
 */
var stripTn = function stripTn(tn) {
  return tn.replace(/^\+?1|[^\d]/g, '');
};

export { stripTn };